.layout-component {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;

  .top {
    width: 100%;
    height: 50px;
    background-color: aquamarine;
    display: flex;
    align-items: center;
  }

  .bottom {
    width: 100%;
    height: calc(100% - 50px);
    display: flex;

    .left {

      width: 200px;
      height: 100%;
      padding-right: 16px;
      background-color: bisque;
    }

    .right {
      width: calc(100% - 200px);
      height: 100%;
      padding: 0 10px;
    }
  }
}
